import React, { useState } from 'react';

const RoleEditor = ({ role, permissions, onSave, onCancel }) => {
  const [roleName, setRoleName] = useState(role?.name || '');
  const [rolePermissions, setRolePermissions] = useState(role?.permissions || []);

  const togglePermission = (permission) => {
    setRolePermissions((prev) =>
      prev.includes(permission)
        ? prev.filter((p) => p !== permission)
        : [...prev, permission]
    );
  };

  const handleSubmit = () => {
    onSave({ ...role, name: roleName, permissions: rolePermissions });
  };

  return (
    <div className="role-editor">
      <h3>{role ? 'Edit Role' : 'Create Role'}</h3>
      <input
        type="text"
        value={roleName}
        onChange={(e) => setRoleName(e.target.value)}
        placeholder="Role Name"
      />
      <div className="permissions">
        {permissions.map((permission) => (
          <label key={permission}>
            <input
              type="checkbox"
              checked={rolePermissions.includes(permission)}
              onChange={() => togglePermission(permission)}
            />
            {permission}
          </label>
        ))}
      </div>
      <button onClick={handleSubmit}>Save</button>
      <button onClick={onCancel}>Cancel</button>
    </div>
  );
};

export default RoleEditor;
